<template>
  <div class="pay-view">
    <div class="pay-count hairline-bottom">
      <p><span>{{ order.total | yuan }}</span></p>
    </div>
    <div class="count-down">
      <countdown v-bind:auto-start="false" ref="countdown" :time="orderTTL">
        <template slot-scope="props"><span class="time-left">{{ props.minutes }}:{{ props.seconds }}</span></template>
      </countdown>
    </div>
    <div class="wx-pay-btn-wrap">
      <a href="javascript:;" @click="handleWxPay" class="wx-pay-btn"></a>
    </div>
  </div>
</template>

<script>
  import { getOrder, onOrderPaySuccess, onApmPaySuccess } from '../api/api'
  import { Toast } from 'mint-ui'
  import { isDevEnv, orderTTL } from '../config/app'

  export default {
    props: ['orderId'],

    name: 'Order',

    data () {
      return {
        order: {},
        showStatus: false,
        orderTTL: orderTTL
      }
    },

    methods: {
      handleWxPay () {
        if (isDevEnv) {
          Toast('')
        }

        if (this.order.paid) {
          this.$toast('')
          return
        }

        let expired = (Date.now() - this.order.creationTime) >= orderTTL
        if (expired) {
          Toast('')
          return
        }

        this.showStatus = !this.showStatus
        let signFields = this.order.signFields
        let { timeStamp, nonceStr, signType } = signFields
        let that = this
        /* eslint-disable */
        wx.ready(function () {
          wx.chooseWXPay({
            timestamp: timeStamp, // jssdktimestamptimeStampS
            nonceStr, //  32 
            package: signFields.package, // prepay_idprepay_id=***
            signType, // 'SHA1''MD5'
            paySign: that.order.paySign, // 
            success: function (res) {
              Toast('')
              if (!that.order.prodId) {
                onApmPaySuccess(that.order.orderId).then(() => {
                  this.shopId = that.$route.params.shopId
                  this.apmId = that.$route.params.apmId
                  that.$router.push({ name: 'RateForm', params: { shopId: that.order.shopId, apmId: that.order.apmId }, query: { state: 'rate' } })
                }).catch(e => {
                  console.error(e)
                  Toast(`${e.message}`)
                })
              } else {
                onOrderPaySuccess(that.order.orderId).then(() => {
                  that.$router.push({ name: 'MyTicket' })
                }).catch(e => {
                  console.error(e)
                  Toast(`${e.message}`)
                })
              }
            },
            fail: function (e) {
              that.$router.push({ name: 'PayFail' })
              Toast('')
            }
          })
        })
      }
    },

    mounted () {
      this.$refs.countdown.start()
      if (!isDevEnv) {
        getOrder(this.orderId).then(data => {
          Toast('')
          this.order = data.data.order
          this.orderTTL = orderTTL - this.order.creationTime
        }).catch(e => {
          console.error(e)
          Toast('')
        })
      }
    }
  }
</script>

<style lang="scss">
.pay-count {
  background-color: whitesmoke;
}
.pay-count p {
  margin: 0;
  padding: 12px;
  text-align: right;
}
.pay-count p span {
  color: red;
  font-weight: bold;
}
.weixin {
  color: green;
}

.count-down {
  padding: 8px 12px;
  background: #fffff6;
  font-size: 12px;
  color: #ea8800;
}

.wx-pay-btn-wrap {
  margin-top: 50px;
  padding: 0 15px;
  a {
    position: relative;
    display: block;
    margin-left: auto;
    margin-right: auto;
    padding-left: 14px;
    padding-right: 14px;
    box-sizing: border-box;
    font-size: 18px;
    text-align: center;
    text-decoration: none;
    color: #FFFFFF;
    line-height: 2.55555556;
    border-radius: 5px;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    overflow: hidden;
    background-color: #1AAD19;
  }
}
</style>



